"use client"

import { useState } from "react"
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query"
import { Package, Plus, Minus, TrendingUp, TrendingDown, History, X } from "lucide-react"
import { formatDateTime } from "@/lib/utils"
import toast from "react-hot-toast"

function InventorySkeleton() {
  return (
    <div className="p-6 lg:p-8">
      <div className="max-w-7xl mx-auto space-y-6">
        <div className="h-8 w-48 bg-gray-200 rounded-lg animate-pulse" />
        <div className="grid grid-cols-2 lg:grid-cols-4 gap-6">
          {[1, 2, 3, 4].map(i => (
            <div key={i} className="h-28 bg-white rounded-2xl animate-pulse" />
          ))}
        </div>
      </div>
    </div>
  )
}

export default function InventoryPage() {
  const queryClient = useQueryClient()
  const [dialogOpen, setDialogOpen] = useState(false)
  const [updateType, setUpdateType] = useState<"add" | "remove">("add")
  const [largeBottles, setLargeBottles] = useState("")
  const [smallBottles, setSmallBottles] = useState("")
  const [reason, setReason] = useState("")

  const { data, isLoading } = useQuery({
    queryKey: ["inventory"],
    queryFn: async () => {
      const res = await fetch("/api/admin/inventory")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const updateMutation = useMutation({
    mutationFn: async () => {
      const largeDelta = parseInt(largeBottles) || 0
      const smallDelta = parseInt(smallBottles) || 0

      const res = await fetch("/api/admin/inventory", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          largeBottlesDelta: updateType === "add" ? largeDelta : -largeDelta,
          smallBottlesDelta: updateType === "add" ? smallDelta : -smallDelta,
          reason,
        }),
      })
      if (!res.ok) {
        const data = await res.json()
        throw new Error(data.error)
      }
      return res.json()
    },
    onSuccess: () => {
      toast.success("Inventory updated")
      queryClient.invalidateQueries({ queryKey: ["inventory"] })
      setDialogOpen(false)
      setLargeBottles("")
      setSmallBottles("")
      setReason("")
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  if (isLoading) return <InventorySkeleton />

  const inventory = data?.data?.inventory
  const recentLogs = data?.data?.recentLogs || []

  return (
    <div className="p-6 lg:p-8">
      <div className="max-w-7xl mx-auto space-y-6">
        
        {/* Page Header */}
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Inventory</h1>
            <p className="text-gray-500 mt-1">Track bottle stock and circulation</p>
          </div>
          <button
            onClick={() => setDialogOpen(true)}
            className="inline-flex items-center gap-2 px-5 py-2.5 bg-slate-900 text-white font-medium rounded-xl hover:bg-slate-800 transition-colors"
          >
            <Plus className="w-5 h-5" />
            Update Stock
          </button>
        </div>

        {/* Update Dialog */}
        {dialogOpen && (
          <div className="fixed inset-0 z-50 flex items-center justify-center">
            <div className="absolute inset-0 bg-black/50" onClick={() => setDialogOpen(false)} />
            <div className="relative bg-white rounded-2xl shadow-xl w-full max-w-md mx-4 p-6">
              <div className="flex items-center justify-between mb-6">
                <div>
                  <h2 className="text-xl font-bold text-gray-900">Update Inventory</h2>
                  <p className="text-sm text-gray-500 mt-1">Add or remove bottles from stock</p>
                </div>
                <button
                  onClick={() => setDialogOpen(false)}
                  className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
                >
                  <X className="w-5 h-5 text-gray-500" />
                </button>
              </div>
              
              {/* Toggle */}
              <div className="flex gap-2 p-1 bg-gray-100 rounded-xl mb-6">
                <button
                  onClick={() => setUpdateType("add")}
                  className={`flex-1 flex items-center justify-center gap-2 py-2.5 rounded-lg text-sm font-medium transition-colors ${
                    updateType === "add" 
                      ? "bg-white text-gray-900 shadow-sm" 
                      : "text-gray-600 hover:text-gray-900"
                  }`}
                >
                  <Plus className="w-4 h-4" />
                  Add Stock
                </button>
                <button
                  onClick={() => setUpdateType("remove")}
                  className={`flex-1 flex items-center justify-center gap-2 py-2.5 rounded-lg text-sm font-medium transition-colors ${
                    updateType === "remove" 
                      ? "bg-white text-gray-900 shadow-sm" 
                      : "text-gray-600 hover:text-gray-900"
                  }`}
                >
                  <Minus className="w-4 h-4" />
                  Remove Stock
                </button>
              </div>

              <div className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1.5">1L Bottles</label>
                    <input
                      type="number"
                      value={largeBottles}
                      onChange={(e) => setLargeBottles(e.target.value)}
                      placeholder="0"
                      min="0"
                      className="w-full h-11 px-4 border border-gray-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1.5">500ml Bottles</label>
                    <input
                      type="number"
                      value={smallBottles}
                      onChange={(e) => setSmallBottles(e.target.value)}
                      placeholder="0"
                      min="0"
                      className="w-full h-11 px-4 border border-gray-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                    />
                  </div>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1.5">Reason</label>
                  <input
                    type="text"
                    value={reason}
                    onChange={(e) => setReason(e.target.value)}
                    placeholder="e.g., New purchase, Damaged bottles"
                    className="w-full h-11 px-4 border border-gray-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                  />
                </div>
              </div>

              <div className="flex gap-3 mt-6">
                <button
                  onClick={() => setDialogOpen(false)}
                  className="flex-1 h-11 px-4 border border-gray-200 rounded-xl font-medium text-gray-700 hover:bg-gray-50 transition-colors"
                >
                  Cancel
                </button>
                <button
                  onClick={() => updateMutation.mutate()}
                  disabled={(!largeBottles && !smallBottles) || !reason || updateMutation.isPending}
                  className={`flex-1 h-11 px-4 rounded-xl font-medium disabled:opacity-50 disabled:cursor-not-allowed transition-colors ${
                    updateType === "remove"
                      ? "bg-red-600 text-white hover:bg-red-700"
                      : "bg-slate-900 text-white hover:bg-slate-800"
                  }`}
                >
                  {updateMutation.isPending ? "Updating..." : (updateType === "add" ? "Add to Inventory" : "Remove from Inventory")}
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Stats Cards */}
        <div className="grid grid-cols-2 lg:grid-cols-4 gap-6">
          <div className="bg-white rounded-2xl p-6 border border-gray-200 shadow-sm">
            <div className="flex items-start justify-between">
              <div>
                <p className="text-sm font-medium text-gray-500 mb-1">Total 1L</p>
                <p className="text-4xl font-bold text-gray-900">{inventory?.largeBottlesTotal || 0}</p>
              </div>
              <div className="w-14 h-14 bg-blue-100 rounded-2xl flex items-center justify-center flex-shrink-0">
                <Package className="w-7 h-7 text-blue-600" />
              </div>
            </div>
          </div>

          <div className="bg-white rounded-2xl p-6 border border-gray-200 shadow-sm">
            <div className="flex items-start justify-between">
              <div>
                <p className="text-sm font-medium text-gray-500 mb-1">Total 500ml</p>
                <p className="text-4xl font-bold text-gray-900">{inventory?.smallBottlesTotal || 0}</p>
              </div>
              <div className="w-14 h-14 bg-purple-100 rounded-2xl flex items-center justify-center flex-shrink-0">
                <Package className="w-6 h-6 text-purple-600" />
              </div>
            </div>
          </div>

          <div className="bg-white rounded-2xl p-6 border border-gray-200 shadow-sm">
            <div className="flex items-start justify-between">
              <div>
                <p className="text-sm font-medium text-gray-500 mb-1">1L Out</p>
                <p className="text-4xl font-bold text-amber-600">{inventory?.largeBottlesInCirculation || 0}</p>
              </div>
              <div className="w-14 h-14 bg-amber-100 rounded-2xl flex items-center justify-center flex-shrink-0">
                <TrendingUp className="w-7 h-7 text-amber-600" />
              </div>
            </div>
          </div>

          <div className="bg-white rounded-2xl p-6 border border-gray-200 shadow-sm">
            <div className="flex items-start justify-between">
              <div>
                <p className="text-sm font-medium text-gray-500 mb-1">500ml Out</p>
                <p className="text-4xl font-bold text-orange-600">{inventory?.smallBottlesInCirculation || 0}</p>
              </div>
              <div className="w-14 h-14 bg-orange-100 rounded-2xl flex items-center justify-center flex-shrink-0">
                <TrendingUp className="w-6 h-6 text-orange-600" />
              </div>
            </div>
          </div>
        </div>

        {/* Bottle Status Cards */}
        <div className="grid md:grid-cols-2 gap-6">
          {/* 1L Status */}
          <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
            <div className="px-6 py-5 border-b border-gray-200">
              <h2 className="text-lg font-semibold text-gray-900">1L Bottle Status</h2>
            </div>
            <div className="p-6 space-y-4">
              <div className="flex items-center justify-between">
                <span className="text-gray-500">Total Stock</span>
                <span className="font-semibold text-gray-900">{inventory?.largeBottlesTotal || 0}</span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-gray-500">With Customers</span>
                <span className="font-semibold text-amber-600">{inventory?.largeBottlesInCirculation || 0}</span>
              </div>
              <div className="h-px bg-gray-200" />
              <div className="flex items-center justify-between">
                <span className="font-medium text-gray-700">Available</span>
                <span className="text-xl font-bold text-emerald-600">{inventory?.largeBottlesAvailable || 0}</span>
              </div>
              <div className="h-3 bg-gray-100 rounded-full overflow-hidden">
                <div
                  className="h-full bg-emerald-500 rounded-full transition-all duration-500"
                  style={{
                    width: `${inventory?.largeBottlesTotal > 0
                      ? ((inventory?.largeBottlesAvailable || 0) / inventory?.largeBottlesTotal) * 100
                      : 0}%`
                  }}
                />
              </div>
            </div>
          </div>

          {/* 500ml Status */}
          <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
            <div className="px-6 py-5 border-b border-gray-200">
              <h2 className="text-lg font-semibold text-gray-900">500ml Bottle Status</h2>
            </div>
            <div className="p-6 space-y-4">
              <div className="flex items-center justify-between">
                <span className="text-gray-500">Total Stock</span>
                <span className="font-semibold text-gray-900">{inventory?.smallBottlesTotal || 0}</span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-gray-500">With Customers</span>
                <span className="font-semibold text-amber-600">{inventory?.smallBottlesInCirculation || 0}</span>
              </div>
              <div className="h-px bg-gray-200" />
              <div className="flex items-center justify-between">
                <span className="font-medium text-gray-700">Available</span>
                <span className="text-xl font-bold text-emerald-600">{inventory?.smallBottlesAvailable || 0}</span>
              </div>
              <div className="h-3 bg-gray-100 rounded-full overflow-hidden">
                <div
                  className="h-full bg-amber-500 rounded-full transition-all duration-500"
                  style={{
                    width: `${inventory?.smallBottlesTotal > 0
                      ? ((inventory?.smallBottlesAvailable || 0) / inventory?.smallBottlesTotal) * 100
                      : 0}%`
                  }}
                />
              </div>
            </div>
          </div>
        </div>

        {/* Recent Changes */}
        <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
          <div className="px-6 py-5 border-b border-gray-200 flex items-center gap-3">
            <History className="w-5 h-5 text-gray-400" />
            <h2 className="text-lg font-semibold text-gray-900">Recent Changes</h2>
          </div>
          
          {recentLogs.length === 0 ? (
            <div className="p-12 text-center">
              <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <History className="w-8 h-8 text-gray-400" />
              </div>
              <p className="text-gray-500 font-medium">No inventory changes yet</p>
              <p className="text-sm text-gray-400 mt-1">Updates will appear here when you add or remove stock</p>
            </div>
          ) : (
            <div className="p-6 space-y-3">
              {recentLogs.map((log: any) => (
                <div key={log.id} className="flex items-center justify-between p-4 bg-gray-50 rounded-xl">
                  <div className="flex items-center gap-4">
                    {log.largeBottlesDelta >= 0 && log.smallBottlesDelta >= 0 ? (
                      <div className="w-10 h-10 bg-emerald-100 rounded-xl flex items-center justify-center flex-shrink-0">
                        <TrendingUp className="w-5 h-5 text-emerald-600" />
                      </div>
                    ) : (
                      <div className="w-10 h-10 bg-red-100 rounded-xl flex items-center justify-center flex-shrink-0">
                        <TrendingDown className="w-5 h-5 text-red-600" />
                      </div>
                    )}
                    <div>
                      <p className="font-medium text-gray-900">{log.reason}</p>
                      <p className="text-sm text-gray-500">{formatDateTime(log.createdAt)}</p>
                    </div>
                  </div>
                  <div className="text-right">
                    {log.largeBottlesDelta !== 0 && (
                      <p className={`text-sm font-semibold ${log.largeBottlesDelta > 0 ? "text-emerald-600" : "text-red-600"}`}>
                        {log.largeBottlesDelta > 0 ? "+" : ""}{log.largeBottlesDelta} × 1L
                      </p>
                    )}
                    {log.smallBottlesDelta !== 0 && (
                      <p className={`text-sm font-semibold ${log.smallBottlesDelta > 0 ? "text-emerald-600" : "text-red-600"}`}>
                        {log.smallBottlesDelta > 0 ? "+" : ""}{log.smallBottlesDelta} × 500ml
                      </p>
                    )}
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

      </div>
    </div>
  )
}
